import React from 'react';
import {Link} from 'react-router-dom';

const Navbar = () => {
  return (
      <div className="sideNavbar">
        <Link to="/TodoList" className='list'>ToDoList </Link>
        <Link to="/Pomodoro" className='list'>Pomodoro </Link>
        <Link to="/Support" className='list'>Support</Link>
        <Link to="/About" className='list'>About</Link>
      </div>
    );
  }
   
  export default Navbar;